import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { BsDatepickerConfig } from 'ngx-bootstrap';
import { DatePipe } from '@angular/common';
import { AuctionHistoryService } from 'src/app/Services/Reports/auction-history.service';
import { CommonService } from 'src/app/Services/common.service';
import { isNullOrUndefined } from '@swimlane/ngx-datatable';

@Component({
  selector: 'app-auction-history-report',
  templateUrl: './auction-history-report.component.html',
  styles: []
})
export class AuctionHistoryReportComponent implements OnInit {
  public dpConfig: Partial<BsDatepickerConfig> = new BsDatepickerConfig();
  date: any;
  month: any;
  branchdetails: any=[]
  public today: Date = new Date();
  branch: any;
  groupcodedetails:any=[]
  groupcode: any;
  AuctionDetails: any=[]
  constructor(private formbuilder:FormBuilder,private datePipe:DatePipe,private auctionservice:AuctionHistoryService,private _CommonService:CommonService) 
  {
    this.dpConfig.dateInputFormat = 'DD-MMM-YYYY'
    this.dpConfig.maxDate = new Date();
    this.dpConfig.showWeekNumbers = false;
   }
  AuctionHistoryReport:FormGroup;
  AuctionHistoryReporterrors:any;
  
  
  ngOnInit() 
  {
    this.month=this.today
    this.date = this.datePipe.transform(this.month, "dd-MMM-yyyy");
    this.GetformData()
    this.GetBranchDetails()
    this.GetGroupCodes()
    this.BlurEventAllControll(this.AuctionHistoryReport)
  }
  public GetformData() {
    this.AuctionHistoryReporterrors = {}
    this.AuctionHistoryReport = this.formbuilder.group({
    
      branch: [null, Validators.required],
      groupcode:[null,Validators.required],
      fromDate: [new Date()],
     
    });
   
  }

  DateChange(event)
  {
    debugger
     this.month=event
    this.date = this.datePipe.transform(this.month, "dd-MMM-yyyy");
    this.GetBranchDetails()
  }
  GetBranchDetails()
  {
    debugger
     this.auctionservice.GetBranchDetails(this.date).subscribe(json=>{
       this. branchdetails=json
       console.log(this.branchdetails)
     })
  }
  Branchchange(event)
  {
    debugger
    if(event!=undefined)
    {
      this.branch=event.branch
      this.GetGroupCodes()
    }
    else{
      this.AuctionHistoryReport.controls.groupcode.setValue("")
    }
    
  }
  GetGroupCodes()
  {
    debugger
    if(this.branch!=undefined)
    {
      this.auctionservice.GetGroupCodes(this.branch,this.date).subscribe(json=>{
        this.groupcodedetails=json
      })
    }
      
    
  
  }
  GroupChange(event)
  {
    if(event!=undefined)
    {
      this.groupcode=event.groupcode
    }
    
  }
  generatereport()
  {
    let isValid: boolean = true;
    if(this.checkValidations(this.AuctionHistoryReport,isValid))
    {
      this.auctionservice.GetAuctionDetails(this.branch,this.groupcode,this.date).subscribe(json=>{
        var temp = [];
        debugger;
        //temp =angular.fromJson(json); 
       let test= JSON.parse(json['chathistory']);
       this.AuctionDetails=test.chatdata
    
    console.log(test);
      })
    }
  }
  checkValidations(group: FormGroup, isValid: boolean): boolean {
    try {
      Object.keys(group.controls).forEach((key: string) => {
        isValid = this.GetValidationByControl(group, key, isValid);
      })
    }
    catch (e) {
      return false;
    }
    return isValid;
  }

  GetValidationByControl(formGroup: FormGroup, key: string, isValid: boolean): boolean {
    try {

      let formcontrol;
      formcontrol = formGroup.get(key);
      if (formcontrol) {
        if (formcontrol instanceof FormGroup) {
          this.checkValidations(formcontrol, isValid)
        }
        else if (formcontrol.validator) {
          this.AuctionHistoryReporterrors[key] = '';
          if (formcontrol.errors || formcontrol.invalid || formcontrol.touched || formcontrol.dirty) {

            let errormessage;
            for (const errorkey in formcontrol.errors) {
              if (errorkey) {
                let lablename;
                lablename = (document.getElementById(key) as HTMLInputElement).title;
                errormessage = this._CommonService.getValidationMessage(formcontrol, errorkey, lablename, key, '');
                this.AuctionHistoryReporterrors[key] += errormessage + ' ';
                isValid = false;
              }
            }
          }
        }
      }
    }
    catch (e) {
      return false;
    }
    return isValid;
  }
  BlurEventAllControll(fromgroup: FormGroup) {
    try {
      Object.keys(fromgroup.controls).forEach((key: string) => {
        this.setBlurEvent(fromgroup, key);
      })
    }
    catch (e) {
      return false;
    }
  }
  setBlurEvent(fromgroup: FormGroup, key: string) {
    try {
      let formcontrol;
      formcontrol = fromgroup.get(key);
      if (formcontrol) {
        if (formcontrol instanceof FormGroup) {
          this.BlurEventAllControll(formcontrol)
        }
        else {
          if (formcontrol.validator)
            fromgroup.get(key).valueChanges.subscribe((data) => { this.GetValidationByControl(fromgroup, key, true) })
        }
      }
    }
    catch (e) {
      return false;
    }
  }
}
